header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*=================BTN=================*/

.cv{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center; 
}

/*=================Socials=================*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0%;
    bottom: 3rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*=================Me=================*/
.me {
    background: linear-gradient( var(--color-primary), transparent);
    width: 22rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 4rem 4rem 6rem 6rem;
    overflow: hidden;
}



/*=================Scroll=================*/

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1.2rem;
}


/*================Icons==================*/
.socials__icon{
    font-size: 1.2rem;
}


/*================Media Query==================*/

@media screen and (max-width: 1024px) {
    .header {
        height: 68vh;
    }    
}

@media screen and (max-width: 600px) {
    .header {
        height: 100vh;
    }   

    .header__socials, .scroll__down{
        display: none;
    }
}